// app/blog/[slug]/page.tsx or page.js
'use client';

export default async function Page({ params }) {
  const { slug } = params;

  // You could fetch real blog post data here using the slug

  return (
    <div className="min-h-screen bg-gray-50 text-gray-800 px-4 py-10">
      <div className="max-w-3xl mx-auto">
        {/* Header */}
        <header className="mb-10">
          <h1 className="text-4xl font-bold tracking-tight mb-2 capitalize">
            {slug.replaceAll('-', ' ')}
          </h1>
          <p className="text-sm text-gray-500">Published on August 2, 2025</p>
        </header>

        {/* Cover Image */}
        <img
          src={`https://source.unsplash.com/960x400/?${slug}`}
          alt="Blog cover"
          className="rounded-xl shadow-md mb-8"
        />

        {/* Content */}
        <article className="prose lg:prose-lg prose-slate">
          <p>
            Welcome to this blog post about <strong>{slug}</strong>. This is a placeholder
            for rich blog content. You can fetch and render Markdown or HTML here from a CMS or file.
          </p>

          <h2>Subheading</h2>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
          </p>

          <ul>
            <li>Point one about the topic</li>
            <li>Another insight</li>
            <li>Something to consider</li>
          </ul>

          <blockquote>
            "Code is like humor. When you have to explain it, it’s bad." – Cory House
          </blockquote>

          <p>Thank you for reading!</p>
        </article>

        {/* Footer */}
        <footer className="mt-16 border-t pt-6 text-center text-sm text-gray-500">
          © {new Date().getFullYear()} My Blog. All rights reserved.
        </footer>
      </div>
    </div>
  );
}
